<template>
  <div
    v-show="style"
    :class="{
      modal: true,
      fade: true,
      show: show,
      'modal-padding': style,
    }"
    :aria-hidden="!show"
  >
    <div
      :class="classBody"
      role="document"
    >
      <div class="modal-content">
        <div class="modal-header">
          <h5
            id="exampleModalLabel"
            class="modal-title"
          >
            {{ title }}
          </h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            @click="closeIt()"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div
          class="modal-body"
          style="text-align: center;font-size:16px"
        >
          <slot />
        </div>
        <div class="modal-footer">
          <button
            v-if="cancel"
            type="button"
            class="btn btn-outline-secondary"
            style="width:85px;"
            @click="closeIt()"
          >
            取消
          </button>
          <button
            type="button"
            class="btn btn-success"
            style="width:85px;"
            data-dismiss="modal"
            @click="confirm()"
          >
            {{ buttonC }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export function show() {
  console.log('show');
}

export default {
  name: 'Modal',
  components: {
  },
  props: {
    popup: {
      type: Boolean,
      default() {
        return false;
      },
    },
    title: {
      type: String,
      default() {
        return '系統提示';
      },
    },
    buttonC: {
      type: String,
      default() {
        return '確定';
      },
    },
    classBody: {
      type: Object,
      default() {
        return this.classOrig;
      },
    },
    cancel: {
      type: Boolean,
      default() {
        return true;
      },
    },
  },
  data() {
    return {
      show: false,
      style: false,
      classOrig: {
        'modal-dialog': true,
        'modal-mid': true,
        'modal-dialog-centered': true,
      },
    };
  },
  computed: {
  },
  watch: {
    popup(value) {
      this.style = value;
      setTimeout(() => {
        this.show = value;
      }, 1);
    },
  },
  created() {
  },
  methods: {
    closeIt() {
      // document.body.classList.remove('modal-open')
      // document.body.style.paddingRight = null
      this.$emit('finish');
    },
    confirm() {
      this.$emit('confirm');
    },
  },
};

</script>

<style scoped>
.modal-padding {
  padding-right: 17px;
  display: block;
}

@media (max-width: 991px) {
  .modal-dialog {
      max-width: unset;
      margin: 1.75rem;
  }
}
</style>
